import { Rule } from 'type/rule'
import { Context } from 'type/context'
import { RuleUpdate, UpdateContext } from 'type/update'
import { update } from '..'
import { tap, use, flatten } from 'utils'
import { findTargetElements } from './element'
import { ruleToUpdates } from './update'
import { registerEvent, getProp } from './web'
import { getEntity, getLastParent, getEntityType } from 'utils/path'
import { Schema } from 'type/schema'

export const isWindow = (t: string) => { return t === 'window'; }

export const isWindowRule = (rule: Rule) => isWindow(rule.input.target)

export const loadRule: Rule = {
  input: { event: 'load', target: 'window' },
  outputs: [{ action: 'initData', target: '' }]
}

export const syncRule: Rule = {
  input: { event: 'click', target: '__sync' },
  outputs: [{ action: 'sync', target: '' }]
}

export const createInputRule = (target: string): Rule => ({
  input: { event: 'change', target },
  outputs: [{ action: 'input', target }]
})

export const registerRule = (context: Context, ru: RuleUpdate) =>
  findTargetElements(getInputTarget(ru.input.target, context.schema), ru.parent)
    .filter(e => !ru.parent ||
      use(getLastParent(getProp('path', e)))(
        p => !p || (tap(getEntityType(context.schema, p)) === tap(getProp('type', ru.parent)))
      )
    )
    .forEach(registerEvent(
      ru.input.event,
      e => ru.outputs.forEach(o => update(context, ruleToUpdates({ context, ...o, eventData: e })))
    ))

const getInputTarget = (target: string, schema: Schema) =>
  getEntity(schema, target) ? 'name' : target