import { UIElement, UIWindow } from 'type/ui'
import { DataUpdate, RuleUpdate, UpdateContext, Update } from 'type/update'
import { Context } from 'type/context'
import { NestedList } from 'type/lang'
import { isString, isDefined, isObject, isArray, isNumber, isListField } from 'type/guard'
import format from 'type/format'
import { tap, when, run, use } from 'utils'
import { pre, get, getEntityType, getFields, getObjectFieldValue, getEntity } from 'utils/path'
import { setElementValue, pathToElements as _pathToElements, getWindowElement, setProp, uiOps } from './web'
import { isWindow } from './rule'
import { createDataUpdate } from './update'
import { updateListUIElement } from './list'
import { ListOp } from 'type/enum'
import { last } from 'ramda'

const updateUIElementValue = ({ path, value, parent, itemType, schema }: UpdateContext) =>
  pathToElements(path, parent).forEach(e => {
    setElementValue(
      isDefined(value) ? format(getEntityType(schema, path, itemType), value) : ''
    )(e)
    updatePath(path)(e)
  })

const updatePath = (path: string) => (e: UIElement) => setProp('path', path, e)

const updateObjectUIElement = (ctx: UpdateContext): RuleUpdate[] =>
  use(ctx.value as object, ctx.rules.map(r => pre(ctx.path, r.input.target)))((v, rs) => {
    rs.forEach(r => pathToElements(r, ctx.parent).forEach(updatePath(r)))
    return getFields(ctx.schema, ctx.itemType).concat({ name: '_uid', type: '' }).reduce<RuleUpdate[]>(
      (l, f) => [
        ...l,
        ...(f.name === '_uid' && ctx.parent && v[f.name]
          ? (setProp(f.name, v[f.name], ctx.parent), [])
          : updateUIElement({
            ...ctx,
            path: pre(ctx.path, f.name),
            itemType: getEntity(ctx.schema, f.name) ? f.name : ctx.itemType,
            value: getObjectFieldValue(ctx, f.name),
            listOp: isListField(f) ? ListOp.replace : 0
          }))
      ],
      []
    )
  })

export const updateUIElement = when<UpdateContext, RuleUpdate[]>(
  [],
  [c => c.uiOp, c => pathToElements(c.path).forEach(uiOps[c.uiOp])],
  [c => !isDefined(c.value), updateUIElementValue],
  [c => c.listOp > 0, updateListUIElement],
  [c => isString(c.value) || isNumber(c.value), updateUIElementValue],
  [c => isObject(c.value), updateObjectUIElement]
)

export const findTargetElements = (target: string, parent?: UIElement): (UIElement | UIWindow)[] =>
  isWindow(target) ? [getWindowElement()] : pathToElements(target, parent)

export const pathToElements = (path: string, parent?: HTMLElement): HTMLElement[] =>
  !path && parent
    ? [parent]
    : _pathToElements(parent ? last(path.split('.')) : path, parent)