import { last } from 'ramda'
import { UIElement } from 'type/ui'
import { Value, Rule } from 'type/rule'
import { Context, Template } from 'type/context'
import { ListOp } from 'type/enum'
import { RuleUpdate, UpdateContext } from 'type/update'
import { NestedList } from 'type/lang'
import { isUID, isNumber, isObject, isArray } from 'type/guard'
import { use, run, seq, returnIfEmpty, cases, tap, lastSplit2, fget, flatten } from 'utils'
import { cloneElement, insertToList, clearList as _clearList, deleteFromList as _deleteFromList } from './web'
import { createDataUpdate, rulesToUpdates } from './update'
import { updateUIElement, pathToElements } from './element'
import { idx2Zero, pathToArray } from 'utils/path'

// steps

const get_listPath_idx = (path: string) =>
  use(lastSplit2('.')(path))(a => isNaN(+a[1]) ? [path, 0] : a)

const get_template_itemType = (listPath: string, templates: Template[]) =>
  fget(t => t.listPath === idx2Zero(listPath), t => [t.element, t.type], templates) || []

const make_clone = (template: UIElement, value: Value) =>
  (value as []).map(v => cloneElement(template))

const _clear_clone_sublist = (clone: UIElement[], value: Value, path: string) =>
  (value as []).map((x, i) =>
    isObject(x) && Object.entries(x).forEach(([k, v]) => isArray(v) && pathToElements(k, clone[i]).forEach(_clearList))
  )

const build_cloneUpdates = (value: Value, _ctx: UpdateContext) =>
  flatten((value as []).map((v, i) =>
    updateUIElement({ ..._ctx, path: lastSplit2('.')(_ctx.path)[0] + '.' + i, listOp: 0, value: v, parent: _ctx['clone'][i] })
  ))

const _insert_clone_to_list = (listPath: string, clone: UIElement[], idx: string, parent: UIElement) =>
  pathToElements(listPath, parent).forEach(l => clone && insertToList(clone, l, +idx))

const get_ruleUpdates = (rules: Rule[], clone: UIElement[], listPath: string, itemType: string, cloneUpdates: RuleUpdate[]) =>
  cloneUpdates.concat(
    flatten(
      clone.map(c => rulesToUpdates(rules, c, listPath, itemType))
    )
  )

const init_path = (path: string) => path + '.0'

// actions

const addToList = seq<UpdateContext, RuleUpdate[]>(
  //tap,
  get_listPath_idx,
  //tap,
  get_template_itemType,
  //tap,
  returnIfEmpty('template', []),
  make_clone,
  _clear_clone_sublist,
  //tap,
  build_cloneUpdates,
  //tap,
  _insert_clone_to_list,
  //tap,
  get_ruleUpdates,
)

const clearList = ({path}) =>
  pathToElements(path).forEach(_clearList)

const deleteFromList = ({path, value}) =>
  pathToElements(path).forEach(e => _deleteFromList(e, value))

const filterList = seq<UpdateContext, RuleUpdate[]>(
  clearList,
  init_path,
  addToList
)

export const updateListUIElement = cases<UpdateContext, RuleUpdate[]>(
  c => c.listOp, [],
  [ListOp.add, addToList],
  [ListOp.clear, clearList],
  [ListOp.delete, deleteFromList],
  [ListOp.replace, filterList],
  [ListOp.filter, filterList],
  [ListOp.sort, filterList],
)
