import { isArray, isObject, isDefined } from 'type/guard'
import { UID } from 'type/ui'
import { max, tap } from '.'

export const addUID = (x: any, uid?: number) =>
  isArray(x) ? x.forEach((y, i) => addUID(y, i)) : (
    isObject(x) ? Object.values(x).forEach(addUID) : (
      isDefined(uid) && (x._uid = uid)
    ))

export const nextUID = <T extends UID>(l: T[]) =>
  l.length === 0 ? 1 : max(l.map(t => isDefined(t._uid) ? t._uid : -1)) + 1