import { reduce, isNil, init, last } from "ramda"
import { NestedList, CaseMap, CasePair, WhenPair, Seq, Return, Case } from 'type/lang'
import { isUndefined, isDefined, isFunction, isArray, isCasePairList, isBoolean } from 'type/guard'
export const isProd = window.location.href.indexOf('localhost') === -1;
export const host = isProd ? (process.env.host ? process.env.host : '') + '/.netlify/functions/' : 'http://localhost:9000/';
export const apiUrl = host + 'api';
export const adminUrl = host + 'admin?';

export const api = ({ doc }) => fetch(`${apiUrl}?doc=${doc}`).then(r => r.json());

export const post = ({ doc, body }) => fetch(`${apiUrl}?doc=${doc}`, { method: 'post', body: JSON.stringify(body) }).then(r => r.json());

export const tap = <T>(x: T, title: string = '', f: (t: T) => any = t => t, pred: boolean | ((t: T) => boolean) = true): T => {
  (isFunction(pred) ? pred(x) : pred) && console.log(title ? `${title} - ` : '', f(x))
  return x
}

export const run = <C>(...fns: Function[]) => (ctx: C): C =>
  fns.reduce((o, f) => {
    Object.entries(f(o) || {}).forEach(([k, v]) => o[k] = v);
    return o;
  }, ctx)

export const seq = <C, R>(f1: Function, ...fns: Function[]) => (ctx: C): R =>
  use(init(fns), last(fns))((fs, f) => f
    ? f(
      [f1].concat(fs).reduce((o, f) => ({ ...o, ...(f(o) || {}) }), ctx)
    )
    : f1(ctx)
  )

export const returnIfEmpty = (p: string, d: any) => props =>
  isUndefined(props[p]) ? { _return: d } : {}

export const use = <T extends any[]>(...args: T) =>
  <R>(f: (...args: T) => R) => f(...args)

export const fget = <T, R>(f: (t: T) => boolean, m: (t: T) => R, a: T[]): R | undefined =>
  use(a.find(f))(t => isNil(t) ? undefined : m(t))

export const max =
  reduce((a: number, b: number) => a >= b ? a : b, Number.NEGATIVE_INFINITY);

export const min =
  reduce((a: number, b: number) => a <= b ? a : b, Number.POSITIVE_INFINITY);

export const both = <T>(f1: (t: T) => boolean, f2: (t: T) => boolean) => (t: T) => f1(t) && f2(t)
export const or = <T>(f1: (t: T) => boolean, f2: (t: T) => boolean) => (t: T) => f1(t) || f2(t)

export const oneOf = <T>(...values: T[]) => (t: T): boolean => values.some(v => v === t)

export const split2 = (d: string, s: string) =>
  splitAt(s.indexOf(d), s)

export const lastSplit2 = (d: string) => (s: string) =>
  splitAt(s.lastIndexOf(d), s)

export const splitAt = (i: number, s: string) =>
  i === -1 ? [s, ''] : [s.slice(0, i), s.slice(i + 1)]

export const flatten = <T>(l: NestedList<T>): T[] =>
  l.reduce((r: T[], n) => isArray(n)
    ? [...r, ...flatten(n)]
    : [...r, n], [])

export const cases = <C, R>(k: Case | ((c: C) => Case), d: R | ((ctx: C) => R), ...cs: CasePair<C, R>[] | [CaseMap<C, R>]) => (ctx: C): R =>
  use(isFunction(k) ? k(ctx) : ctx[k], () => isFunction(d) ? d(ctx) : d)((kv, dv) =>
    isCasePairList(cs)
      ? fget(t => kv == t[0], t => t[1](ctx) || dv() , cs) || dv()
      : (cs[0][kv] && cs[0][kv](ctx) || dv())
  )

export const when = <C, R>(d: R | ((ctx: C) => R), ...ws: WhenPair<C, R>[]) => (ctx: C): R =>
  use(() => isFunction(d) ? d(ctx) : d)(dv =>
    fget(t => t[0](ctx), t => t[1](ctx) || dv(), ws) || dv()
  )

export const fmap = <T, R>(f: (t: T) => boolean, m: (t: T) => R, a: T[]): R[] =>
  a.filter(f).map(m)

// partial map
export const pmap = <T>(f: (t: T) => boolean, m: (t: T) => T, a: T[]): T[] =>
  a.map(x => f(x) ? m(x) : x)

export const ffor = <T>(f: (t: T) => boolean, l: (t: T) => void, a: T[]): void =>
  a.filter(f).forEach(l)

export const isStringLiteral = (s: string) =>
  s && s.length > 1 && s[0] === '"' && s[s.length - 1] === '"'

export const getStringLiteral = (s: string) =>
  isStringLiteral(s) ? s.slice(1, s.length - 1) : undefined