import { Schema, Field, ObjectField, ListField } from 'type/schema'
import { Template } from 'type/context'
import { isListField, isTemplateField, isObjectField, isDefined } from 'type/guard'
import { use, when, flatten, tap } from 'utils'
import { pre, getField, getEntity } from 'utils/path'
import { pathToElements } from './element'
import { UIElement } from 'type/ui'
import { clearParentList, setProp } from './web'

export const fieldsToTemplates = (schema: Schema, parentPath: string): Template[] =>
  flatten(schema.map(fieldToTemplates(schema, parentPath)))

const fieldToTemplates = (schema: Schema, parentPath: string, parentIsList: boolean = false) => when<Field, Template[]>(
    [],
    [f => isTemplateField(f, schema), listFieldToTemplate(schema, parentPath)],
    [isObjectField, f => fieldsToTemplates(f['fields'], pre(parentPath, parentIsList ? '0' : f.name))]
  )

const listFieldToTemplate = (schema: Schema, parentPath: string) => (field: Field): Template[] =>
  use(pre(parentPath, field.name))(listPath =>
    use(pathToElements(listPath + '.0'))(elements =>
      elements.length > 0
        ? createTemplate(listPath, field, elements[0]).concat(listItemToTemplates(schema, listPath)(field))
        : []
    )
  )

const createTemplate = (listPath: string, field: Field, element: UIElement) => {
  const type = isListField(field) ? field.itemType : field.type
  setProp('type', type, element)
  return [{ listPath, type, element }]
}

const listItemToTemplates = (schema: Schema, parentPath: string) => when<Field, Template[]>(
  [],
  [isListField, f => use(getEntity(schema, (f as ListField).itemType))(
    e => e ? fieldToTemplates(schema, parentPath, true)(e) : []
  )]
)
