import { fromPairs } from 'ramda'
import { Rule } from 'type/rule'
import { Data, Context, Template } from 'type/context'
import { Schema } from 'type/schema'
import { Update, DataUpdate, RuleUpdate, EffectUpdate, SetContext } from 'type/update'
import { isDataUpdate, isRuleUpdate, isUIUpdate, isEffectUpdate, isObjectField, isListField } from 'type/guard'
import { updateUIElement } from 'ui/element'
import { fieldsToTemplates } from 'ui/template'
import { loadRule, syncRule, isWindowRule, registerRule } from 'ui/rule'
import { evalUpdate, createRuleUpdate, createUpdateContext } from 'ui/update'
import { flatten, run, pmap, fmap, ffor, tap, use } from 'utils'
import { setAll } from 'utils/path'
import { expandRules } from 'utils/rule'
import { getChildren, getName } from 'ui/web'

// tasks

const expand_schema = (schema: Schema) =>
  flatten(schema.map(f => [f].concat(isObjectField(f) ? f.fields.filter(isListField) : [])))

const expand_rules = (rules: Rule[], schema: Schema) =>
  [loadRule, syncRule, ...expandRules(rules, schema)]

const create_templates = (schema: Schema, _ctx: Context) =>
  _ctx.templates || fieldsToTemplates(schema, '')

const _register_window_rules = (rules: Rule[], _ctx: Context) =>
  rules.filter(isWindowRule).forEach(r => registerRule(_ctx, createRuleUpdate(r)))

const eval_updates = (data: Data, schema: Schema, updates: Update[]) =>
  pmap(isDataUpdate, u => evalUpdate({ ...(u as DataUpdate), data, schema}), updates)

const update_data = (data: Data, updates: Update[]) =>
  setAll(
    ...fmap(isDataUpdate, u => [u['path'], u['value'], u['listOp']] as SetContext, updates)
  )(data)

const flush_updates = (updates: Update[], _ctx: Context) =>
  updates.concat(flatten(fmap(isUIUpdate, u => updateUIElement({ ..._ctx, ...(u as DataUpdate) }), updates)))

const _register_rules = (updates: Update[], _ctx: Context) =>
  ffor(isRuleUpdate, u => registerRule(_ctx, (u as RuleUpdate)), updates)

const _run_effects = (updates: Update[]) =>
  ffor(
    isEffectUpdate,
    u => (u as EffectUpdate).effect(),
    updates
  )


// task lists

export const initialize = run<Context>(
  //({rules}: Context) => ({ rules: expand_rules(rules)}),
  expand_schema,
  expand_rules,
  //tap,
  //c => tap(c.updates, 'i'),
  _register_window_rules,
)

const cycle = run<Context>(
  //tap,
  //c => tap(document.getElementsByName('products'), 'c'),
  create_templates,
  //_clean_templates,
  //tap,
  eval_updates,
  update_data,
  //tap,
  flush_updates,
  //tap,
  _register_rules,
  _run_effects
)

export const update = (ctx: Context, updates: Update[]) => {
  ctx.updates = updates
  cycle(ctx)
}