import { Rule } from 'type/rule'
import { Schema, getField } from 'type/schema'
import { flatten, use, tap } from 'utils'
import { get, set } from 'utils/path'

const paths = ['input.target', 'output.target', 'output.params.item', 'output.params.field', 'output.params.exp.op1', 'output.params.exp.op2', 'output.params.predicate.op1', 'output.params.predicate.op2']

const findVars = (rule: Rule, schema: Schema) =>
  flatten(paths.map(p => ((get(p, rule) || '') as string).match(/\[(\w+)\]/g) || []).map(x => x.slice(1, x.length - 1)))

const getValuesFromSchema = (s: string, schema: Schema) =>
  getField(schema, s, 'options')
  
export const expandRules = (rules: Rule[], schema: Schema) =>
  flatten(rules.map(r => use(findVars(r, schema))(vars =>
    vars.length === 0 ? [r] :
      vars.map(v =>
        use(getValuesFromSchema(v, schema))(vs =>
          vs.map(x => paths.reduce((o, p) => set(p, (get(p, o) as string).replace(new RegExp(`[${x}]`, 'g'), x), o), r))
        )
      )
  )))
