import { is } from 'ramda'
import { Update, DataUpdate, RuleUpdate, EffectUpdate } from './update'
import { Field, ObjectField, ListField, Schema, CalculatedField } from './schema';
import { UID } from './ui'
import { UpdateType } from './enum'
import { CaseMap, CasePair } from './lang'
import { oneOf, fget, tap } from 'utils'
import { Expression, BinaryExpression, AggregateExpression } from './rule';

// lang

export const isUndefined = (x: any): x is undefined =>
  typeof x === 'undefined'

export const isDefined = <T>(x: T | undefined): x is T =>
  typeof x !== 'undefined'

export const isString = (x: any): x is string =>
  typeof x === 'string'

export const isNumber = (x: any): x is number =>
  typeof x === 'number'

export const isBoolean = (x: any): x is boolean =>
  typeof x === 'boolean'

  export const isFunction = (x: any): x is Function =>
  typeof x === 'function'

export const isArray = Array.isArray

export const isObject = (x: any): x is object =>
  is(Object, x)

export const isCasePairList = <I, O>(x: [CaseMap<I, O>] | CasePair<I, O>[]): x is CasePair<I, O>[] =>
  x.length > 0 && isArray(x[0])


// schema

export const isObjectField = (f: Field): f is ObjectField =>
  f.type === 'object'

export const isListField = (f: Field): f is ListField =>
  f.type === 'list'

export const isOptionField = (f: Field, s: Schema) =>
  fget(x => x.name === f.type, x => x.type, s) === 'options'

export const isTemplateField = (f: Field, s: Schema) =>
  isListField(f) || isOptionField(f, s)

export const isCalculatedField = (f: Field): f is CalculatedField =>
  f.type === 'calculated'


// update

export const isDataUpdate = (update: Update): update is DataUpdate =>
  oneOf(UpdateType.data_ui, UpdateType.data)(update.type) 

export const isUIUpdate = (update: Update): update is DataUpdate =>
  oneOf(UpdateType.data_ui, UpdateType.ui)(update.type) 

export const isRuleUpdate = (update: Update): update is RuleUpdate =>
  update.type === UpdateType.rule

export const isEffectUpdate = (update: Update): update is EffectUpdate =>
  update.type === UpdateType.effect


// rule

export const isBinaryExpression = (exp: Expression): exp is BinaryExpression =>
  exp['operator'] ?? exp['op1'] ?? exp['op2']

export const isAggregateExpression = (exp: Expression): exp is AggregateExpression =>
  exp['func'] ?? exp['path'] ?? exp['parent']

// ui

export const isUID = (x: any): x is UID =>
  isDefined(x['_uid'])
