import { contains, sum } from 'ramda'
import { BinaryExpression, AggregateExpression, Value, Expression } from 'type/rule'
import { Data } from 'type/context'
import { isUndefined, isBinaryExpression, isAggregateExpression } from 'type/guard';
import { get } from './path'
import { tap, getStringLiteral, cases, when } from 'utils';

export const evalBinaryExp = (p: BinaryExpression) => (data: Data, x1?: string, y1?: string): string => {
  // const x = (isString(op1) ? op1 : (p.op1 ? get(p.op1)(op1) : '')) as string;
  // const y = (isString(op1) ? op2 : (p.op2 ? get(p.op2)(op1) : '')) as string;

  if (!x1) x1 = getStringLiteral(p.op1)
  if (!y1) y1 = getStringLiteral(p.op2)
  const x = isUndefined(x1) ? (p.op1 ? get(p.op1)(data) : '') : x1
  const y = isUndefined(y1) ? (p.op2 ? get(p.op2)(data) : '') : y1

  let z;
  let op = p.operator;
  let not = false;
  if (op.slice(0, 1) === '!') {
    not = true;
    op = op.slice(1);
  }

  if (op === '~') z = contains(y, x);
  else if (op === '=') z = x == y;
  else {
    const a = +x;
    const b = +y;
    if (op === '+') z = a + b;
    else if (op === '-') z = a - b;
    else if (op === '*') z = a * b;
    else if (op === '/') z = a / b;
    else if (op === '>') z = a > b;
    else if (op === '<') z = a < b;
    else if (op === '>=') z = a >= b;
    else if (op === '<=') z = a <= b;
  }

  if (not) z = !z;

  return z.toString();
}

export const evalAggregateExpression = cases<AggregateExpression, (data: Data) => Value>(
  e => e.func, e => d => '',
  ['sum', e => d => sum((get(e.parent, d) || []).map(x => get(e.path, x))).toString()]
)

export const evalExpression = when<Expression, (data: Data) => Value>(
  e => d => '',
  [isBinaryExpression, e => evalBinaryExp(e as BinaryExpression)],
  [isAggregateExpression, e => evalAggregateExpression(e as AggregateExpression)]
)
