import { use, when, tap } from 'utils'
import { pathToArray } from 'utils/path'
import { isString } from 'type/guard'

export const isCheckBox = (e: HTMLElement): boolean =>
  e instanceof HTMLInputElement && e.type === "checkbox"

export const setElementValue = (v: string) => when<HTMLElement, string>(
  e => e.innerText = v,
  [e => e instanceof HTMLInputElement, e => e['value'] = v],
  [e => e instanceof HTMLOptionElement, e => e['value'] = e.innerText = v],
  [e => e instanceof HTMLSelectElement, e => e['value'] = v],
  [e => e instanceof HTMLImageElement, e => e['src'] = v]
)

export const pathToElements = (path: string, parent?: HTMLElement) =>
  [...(parent || document).querySelectorAll(':scope ' + elementsToSelector(path))] as HTMLElement[];

const elementToSelector = (n: string | number, i: number = 0) => // n: element name or element array index, i: whether the 1st selector
  isString(n)
    ? `${i > 0 ? ' ' : ''}[name="${n}"]`
    : `> :nth-child(${n + 1})`;

const elementsToSelector = (path: string) =>
  pathToArray(path).map(elementToSelector).join('');

export const registerEvent = (event: string, handler: EventListener) => (e: HTMLElement | Window) =>
  e.addEventListener(event, handler)

export const getWindowElement = () => window

export const getTargetElement = (event: any) => ((event as Event).target) as HTMLElement

export const insertToList = (elements: HTMLElement[], list: HTMLElement, index: number) =>
  use(document.createDocumentFragment())(fragment => {
    elements.forEach(e => fragment.appendChild(e as Node));
    list.insertBefore(fragment, list.children[index]);
  })

export const cloneElement = (element: HTMLElement): HTMLElement =>
  element.cloneNode(true) as HTMLElement

export const clearList = (element: HTMLElement) =>
  element.innerHTML = ''

export const clearParentList = (element: HTMLElement) =>
  element.parentElement && (element.parentElement.innerHTML = '')

export const deleteFromList = (element: HTMLElement, uid: string) =>
  use(element.querySelector(`[_uid="${uid}"]`))(e => e && element.removeChild(e))

export const setProp = (key: string, value: string, element: HTMLElement) =>
  element.setAttribute(key, value)

export const getProp = (key: string, element: HTMLElement) =>
  element.getAttribute(key)

export const findAttr = (attr: string, elem: HTMLElement) =>
  elem.getAttribute(attr) || (elem.parentElement ? findAttr(attr, elem.parentElement) : '')

export const getChildren = (element: HTMLElement) =>
  Array.from(element.children).map(c => c as HTMLElement)

export const getAttr = (attr: string) => (element: HTMLElement) =>
  element.getAttribute(attr)

export const getName = getAttr('name')

export const uiOps = {
  show: (element: HTMLElement) =>
    element.style.display = 'block',
  hide: (element: HTMLElement) =>
    element.style.display = 'none'
}