import { curry, pipe, lensPath, view, set as _set, over, remove, sortWith, descend, ascend, prop, last } from 'ramda'
import { BoolMap, LensPath } from 'type/lang'
import { Value, Predicate } from 'type/rule'
import { ListOp } from 'type/enum'
import { Data } from 'type/context'
import { use, tap, cases, lastSplit2, getStringLiteral } from '.'
import { evalBinaryExp } from './expression'
import { DataContext, SetContext, UpdateContext } from 'type/update';
import { createDataContext } from 'ui/update'
import { isArray, isNumber, isObjectField, isListField, isCalculatedField } from 'type/guard'
import { Schema, Field } from 'type/schema'

export const pre = (s1: string, s2: string): string =>
  (s1 ? (s1 + '.') : '') + s2

export const pathToArray = (p: string): LensPath => p
  .replace(/\[/g, ".")
  .replace(/\]/g, "")
  .split(".")
  .map(x => isNaN(+x) ? x : +x)

const lens = pipe(
  pathToArray,
  lensPath
)

export const get = curry(
  <T extends Value>(p: string, o: object) => view<object, T>(lens(p))(o)
)

export const setValue = (c: DataContext): Data =>
  _set(lens(c.path))(c.value, c.data)

export const set = cases<DataContext, Data>(
  c => c.listOp,
  setValue,
  [ListOp.filter, c => c.data],
  [ListOp.add, c => isArray(c.value) ? setAll(...c.value.map((v, i) => use(lastSplit2('.')(c.path))(([listPath, idx]) => [listPath + '.' + (+idx + i), v, ListOp.add] as SetContext)))(c.data) : setValue(c)],
  [ListOp.delete, c => c.value ? over(lens(c.path), l => use(findIndexByUid(l, c.value))(i => i > -1 ? remove(+i, 1, l) : l), c.data) : c.data]
)

const findIndexByUid = (l, uid) =>
  l.every(x => x._uid) ? l.findIndex(x => x._uid == uid) : uid

export const setPath = (v: Value, l: ListOp, o: Data) => (p: string) => set(createDataContext(p, v, l, o))

export const setAll = (...vs: SetContext[]) => (obj: Data) =>
  vs.reduce((o, [k, v, l]) => set(createDataContext(k, v, l, o)), obj)

export const setWhen = <T>(boolMap: BoolMap<T>) => (obj: Data, value: T) =>
  use(Object.entries(boolMap).find(([k, v]) => v(value)))(
    r => r ? set(createDataContext(r[0], value, 0, obj)) : obj
  )

export const filterList = (path: string, predicates: Predicate[] | undefined, data: Data, srcPath: string, schema: Schema) =>
  predicates && use(get(path, data))(list =>
    list.filter(x => predicates.every(p => evalBinaryExp(p)(
      data,
      x[p.op1],
      getStringLiteral(p.op2) || get(findOperandPath(srcPath, p.op1, schema), data) //get(p.op2, data),
    ) === 'true'))
  )

export const sortList = (path: string, field: string, order: string, data: Data) =>
  use(get(path, data))(
    list => sortWith([(order === 'descending' ? descend : ascend)(prop(field))], list)
  )

export const getEntity = (schema: Schema, name: string) =>
  schema.find(f => f.name === name)

export const getFields = (schema: Schema, name: string): Field[] =>
  use(getEntity(schema, name))(
    f => !f || !isObjectField(f) ? [] : f.fields
  )

export const getField = (schema: Schema, entity: string, field: string) =>
  use(getEntity(getFields(schema, entity), field))(e =>
    (e && getEntity(schema, e.type)) || e
  )

export const getEntityType = (schema: Schema, path: string, itemType?: string): string =>
  use(getEntity(schema, itemType || path))(
    f => !f ? '' : (
      isListField(f) ? f.itemType : (
        isObjectField(f) ? use(getEntity(f.fields, last(pathToArray(path))))(f1 => !f1 ? '' : f1.type) : f.type
      )
    )
  )

export const getObjectFieldValue = (ctx: UpdateContext, name: string) =>
  use(getField(ctx.schema, ctx.itemType || ctx.path, name))(f =>
    !f ? '' : (isCalculatedField(f) ? f.format.replace(/{id}/g, ctx.value['id']) : ctx.value[name])
  )

export const idx2Zero = (p: string) =>
  p.split('.').map(x => isNaN(+x) ? x : '0').join('.')

const nameToId = (path: string) =>
  use(lastSplit2('.')(path))(([p1, p2]) => p2 === 'name' ? pre(p1, 'id') : path)

const findOperandPath = (p: string, l: string, schema: Schema) =>
  use(pathToArray(p))(ps =>
    use(ps.findIndex(x => isNumber(x) ? false : getEntityType(schema, x) === l))(i =>
      i > -1 ? `${ps.slice(0, i+2).join('.')}.id` : ''
    )
  )

export const getLastParent = (p: string) =>
  use(pathToArray(p))(ps =>
    ps.length < 2 ? '' : (
      ps.length === 2 ? ps[0] : (
        isNumber(ps[ps.length - 2]) ? ps[ps.length - 3] : ps[ps.length - 2]
    ))
  )

export const removeLast = (p: string) =>
  lastSplit2('.')(p)[0]