import { fget } from 'utils'

export type Schema = Field[]

export type Field = {
  name: string,
  type: string,
}

export type ObjectField = Field & {
  fields: Schema
}

export type ListField = Field & {
  itemType: string
}

export type CalculatedField = Field & {
  format: string
}

export const getField = (schema: Schema, type: string, prop: string) =>
  fget(f => f.name === type, f => f[prop], schema)